
.tn-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 0;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.logo{
    font-size: 1.5rem ;
    font-weight: 600;
    padding: 0 0.4rem;
    letter-spacing: 0.1rem;
    text-decoration: none;
    color: black;
}

.search-box:focus{
    border: 1px solid rgb(0, 0, 0);
}

.search-box{
    width: 555px;
    border: 1px solid rgb(163, 160, 160);
    border-radius: 25px;
    padding: 0.5rem 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .5s ease;
}


.search-box input{
    width: 100%;
    display: block;
    border:none;
    outline: none;
    
}


.search-icon{
    color:rgb(0, 0, 0);
    font-size: 1rem;
}

.tn-icons{
    display: flex;
    padding: 0 20px;
    align-items: center;
    list-style: none;
    gap: 20px;
    
    font-size: 1rem;
}

.tn-icons a{
    font-size: 1.5rem;
    color: rgb(29, 12, 3);
}

.fav-icon, .cart-icon{
    position: relative;
}

.fav-icon .count{
    position: absolute;
    font-size: 1rem;
    top: -0.5rem;
    right: 0rem;
    color:antiquewhite
}

.cart-icon .count{
    position: absolute;
    font-size: 1rem;
    top: -0.5rem;
    right: 0rem;
    color:antiquewhite
}

